.note{
    display: flex;
    flex-direction: column;
    flex: 1;
    height: 100%;
    .note__header{
        height: 100px;
        width: 100%;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        padding: 20px;
        box-sizing: border-box;
        border-bottom: 1px solid #e6e6e6;
        .note__header-date{
            font-size: 14px;
            color: #737373;
        }
        .note__header-action-btn{
            display: flex;
            .action-btn{
                color: #737373;
                font-size: 14px;
                opacity: 0.6;
                margin: 0 10px;
            }
            &:hover{
                opacity: 1;
                cursor: pointer;
            }
        }
    }
    .note__body{
        flex: 1;
        padding: 20px;
    }
    .note__body-head{
        input{
            padding: 10px;
            font-size: 20px;
            border: none;
            outline: none;
            width: 100%;
        }
    }
    .note__body-content{
        textarea{
            width: 100%;
            height: 100%;
            padding: 10px;
            font-size:16px ;
            border:none ;
            outline: none;
        }
    }
}